
.wrapper {

	display: flex;
	flex-direction: column;
	min-width: 320px;
	padding: 16px;
	
	background-color: var(--background-sec);
	overflow: auto;
	z-index: 999;
	border-radius : 6px 6px 0px 0px;
	form {
		flex-grow: 1;
		display: flex;
		gap: 8px;
		textarea{flex-grow: 1;height: 100%;}
		.form-btn-group {
			gap: 5px;
			display: flex;
			flex-direction: column;
		}

		.btnSubmit {
			color: var(--LimeGreen);

			&:hover {
				border-color: var(--LimeGreen);
			}

			&[tabindex]:focus {
				outline-color: var(--LimeGreen);
			}
		}

		.btnReset {
			color: var(--BrightRed);

			&:hover {
				border-color: var(--BrightRed);
			}

			&[tabindex]:focus {
				outline-color: var(--BrightRed);
			}

		}

		button {
			padding: 6px;
			transition: color 0.5s ease;
			background-color: transparent;
			font-size: 1.25rem;
			cursor: pointer;
			border: 2px solid var(--border-color);
			border-radius: 6px;
			color: inherit;
			user-select: none;
			&:disabled {
				pointer-events: none;
				opacity: 0.35;
				cursor: not-allowed;
			}

			&:active {
				border-color: red;
			}
		}
	}
}

@media screen and (min-width: 920px) {
	.wrapper {
		form {

			button {
				height: unset;
				margin-left: 0;
				margin-top: 5px;
				padding: 8px;
			}
		}
	}
}



.input-wrraper {
	padding: 4px 8px;
	width: 88%;
	margin: 0 auto;
	.input {
		-webkit-appearance: none;
		appearance: none;
		background-color: transparent;
		font-size: 1rem;
		font-weight: 300;
		border: 2px solid var(--border-color);
		padding: 4px 8px;
		flex-grow: 1;
		width: 100%;
		height: 100%;
		color: inherit;
		border-radius: 4px;
		outline: 2px transparent solid;
		&[tabindex]:focus {
			outline-color: var(--LimeGreen);
		}
	}
}
