.ViewDetail-wrapper {
	background-color: var(--background-pri);
	border: 2px solid var(--border-color);
	border-radius: 7px;
	padding: 0 16px;
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	display: flex;
	flex-direction: column;
	gap: 32px;
	z-index: 999;
	overflow: overlay;
	.header {
		display: flex;
		min-height: 32px;
		justify-content: flex-end;
		align-items: center;
		font-size: 1.6rem;
		color: var(--LimeGreen);
		.close-form {
			-webkit-appearance: none;
			appearance: none;
			background-color: transparent;
			transition: all 0.5s ease;
			display: inline-flex;
			justify-content: center;
			align-items: center;
			border-radius: 50%;
			font-size: 1rem;
			cursor: pointer;
			border: none;
			color: var(--BrightRed);
			&:hover {
				filter: brightness(1.25);
			}
		}
	}
}
.wrapper {
	max-width: 100%;
	display: flex;
	justify-content: center;
	align-items: flex-start;
	flex-grow: 1;
}
.HanziComponents-wrapper {
	max-width: 100%;
	display: flex;
	justify-content: center;
	align-items: flex-start;
	flex-wrap: wrap;
	gap: 16px;
	flex-basis: 50%;
}
.content-wrapper {
	flex-grow: 1;
	display: flex;
	flex-direction: column;
	gap: 32px;
	.list {
		li {
			list-style: none;
			text-transform: capitalize;
			font-size: 1.125rem;
			span {
				font-weight: bold;
				font-size: 1.6rem;
				color: var(--LimeGreen);
			}
			padding: 6px;
		}
	}
	.note {
		font-size: 1.25rem;
	}
	
}
.footer {
		margin-top: auto;
		display: flex;
		gap: 32px;
		height: 32px;
		display: flex;
	}