.App-viewer {
	flex-grow: 1;
	padding: 15px;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	align-items: center;
	gap: 15px;
	transition: all 0.5s ease;
	overflow: overlay;
}
.view_char_wrapper {
	width: 100%;
	display: flex;
	flex-direction: column-reverse;
}

.wrapper {
	padding-top: 16px;
	display: flex;
	flex-direction: column;
	flex-grow: 1;
	overflow: overlay;
	position: relative;
	gap: 8px;
	&::-webkit-scrollbar {
		display: none;
	}
	.header {
		display: flex;
		height: 50px;
		padding: 15px;
		text-align: center;
		font-size: 1.6rem;
		position: absolute;
		width: 100%;
		top: 0;
		background-color: var(--background-pri);
		.ml-auto {
			margin-left: auto;
			font-size: 1.25rem;
			input[type="range"] {
				width: 80px;
				padding: 0 8px;
			}
		}
	}
}

.form-wrapper {
	margin: auto;
}

.view_pinyin_warapper {
	flex-grow: 1;
	display: flex;
	flex-direction: column;
	height: 100%;
	overflow: overlay;
	.hanzi_ {
		flex-grow: 1;
		display: flex;
		flex-direction: column-reverse;
		gap: 32px;
	}
	.btns {
		padding: 8px 16px;
		display: flex;
		align-items: center;
	}
	.text-wrapper {
		flex-grow: 1;
		padding: 16px 16px 16px 32px;
		.char_wrapper {
			position: relative;
			display: inline-flex;
			flex-direction: column;
			padding: 0 3px;
			font-size: var(--hanzi_size);
			border-bottom: 2px solid transparent;
		}
		.char_pinyin {
			line-height: 1.5;
			font-size: var(--hanzi_size);
			color: var(--LimeGreen);
			user-select: none;
			cursor: default;
		}
		.char_hanzi {
			cursor: default;
		}
	}
}
.slide-input {
	display: flex;
	align-items: center;
	input[type="range"] {
		appearance: none;
		background: var(--background-sec);
		width: 100px;
		border-radius: 6px;
		height: 6px;
		outline: none;
		&::-webkit-slider-thumb {
			appearance: none;
			width: 10px;
			height: 10px;
			border-radius: 50%;
			cursor: ew-resize;
			background: var(--LimeGreen);
		}
	}
	&::before {
		content: "-";
	}
	&::after {
		content: "+";
	}
}
.main {
	display: flex;
	flex-direction: column;
	gap: 32px;
	height: 100%;
	padding: 15px 15px 0 15px;
	background-color: var(--background-pri);
	// overflow: overlay;
	.flex-row {
		height: 100%;
		display: flex;
		gap: 32px;
	}
	.main-content {
		padding: 15px;
		flex-grow: 1;
		border-radius: 6px;
		height: 100%;
		overflow: auto;
		user-select: none;
		&::-webkit-scrollbar {
			display: none;
		}
	}
}

.list-wrapper {
	--Hanzi_FontSize: 1.85rem;
	display: flex;
	flex-wrap: wrap;
	gap: 16px;
	padding:0 16px 16px 16px;
	height: calc(100% - 50px);
	background-color: #121212;
	border-radius: 6px;
	overflow: auto;
	&::-webkit-scrollbar {
		display: none;
	}
	.card {
		position: absolute;
		box-shadow:
		/* offset-x | offset-y | blur-radius | spread-radius | color */
		0px 16px 24px 2px hsla(0,0%,0%,0.14), 
		0px 6px 30px 5px hsla(0,0%,0%,0.12), 
		0px 8px 10px -5px hsla(0,0%,0%,0.2);
		.content {
			position: absolute;
			width: 100%;
			height: 100%;
			transition: transform 1s;
			transform-style: preserve-3d;
			.front,
			.back {
				position: absolute;
				height: 100%;
				width: 100%;
				background: var(--background-sec);
				font-size: var(--Hanzi_FontSize);
				border-radius: 5px;
				backface-visibility: hidden;
				display: flex;
				justify-content: center;
				align-items: center;
				color: var(--text-color);
			}
			.front {
				font-family: "Noto Sans SC", sans-serif;
				font-weight: 700;
			}
			.back {
				display: flex;
				flex-direction: column;
				gap: 8px;
				color: var(--text-color);
				font-size: 1rem;
				background: var(--background-pri);
				transform: rotateX(180deg);
				& > div {
					flex-grow: 1;
					display: flex;
					justify-content: center;
					align-items: center;
					flex-direction: column;
				}
				button {
					display: block;
					width: 100%;
					-webkit-appearance: none;
					appearance: none;
					background-color: transparent;
					font-size: 1rem;
					padding: 6px;
					border: 1px solid var(--border-color);
					color: var(--text-color);
					transition: color 250ms linear;
					cursor: pointer;
					display: block;
					&:hover {
						background-color: var(--background-sec);
					}
				}
			}
		}
		&:hover {
			cursor: pointer;
		}
		&:hover .content {
			transform: rotateX(180deg);
			transition: transform 0.5s;
		}
	}
}

.btns {
	display: flex;
	gap: 8px;
	align-items: center;
	background-color: var(--background-sec);
	border-radius: 6px 6px 0px 0px;
	.wrapper {
		display: flex;
		flex-direction: row-reverse;
		padding: 0px 8px;
		flex-grow: 1;
	}
	button {
		-webkit-appearance: none;
		appearance: none;
		background-color: transparent;
		font-size: 1rem;
		padding: 3px 6px;
		border-radius: 3px;
		border: 1px solid var(--border-color);
		color: var(--text-color);
		transition: color 250ms linear;
		cursor: pointer;
		&:hover {
			color: var(--LimeGreen);
		}
	}
}
@media screen and (max-width: 920px) {
	.btns {
		.wrapper {
			flex-wrap: wrap-reverse;
			justify-content: flex-end;
		}
	}
}
.search-wrapper {
	margin: 0 auto;
	display: flex;
	gap: 16px;
	align-items: center;
	form {
		display: flex;
		gap: 8px;
		align-items: center;
		font-size: 1rem;
		.input-wrapper {
			display: flex;
			gap: 8px;
			align-items: center;
			input[type="search"] {
				height: 45px;
				width: 200px;
				flex-grow: 1;
				background-color: transparent;
				padding: 8px 16px;
				border: none;
				transition: all 250ms ease;
				outline: none;
				color: inherit;
				&:focus::placeholder {
					color: var(--LimeGreen);
				}
			}
			button {
				display: inline-block;
				height: 50px;
				padding: 8px 16px;
				-webkit-appearance: none;
				appearance: none;
				background-color: transparent;
				color: var(--text-color);
				transition: color 250ms linear;
				outline: none;
				cursor: pointer;
				&:focus {
					color: var(--LimeGreen);
				}
			}
		}
		select {
			-webkit-appearance: none;
			appearance: none;
			background-color: transparent;
			border: 1px solid var(--border-color);
			border-radius: 3px;
			padding: 3px 6px;
			color: inherit;
			text-align: center;
			option {
				background-color: var(--background-sec) !important;
			}
		}
	}
}

.test-wrapper {
	position: relative;
	flex-grow: 1;
	display: flex;
	justify-content: center;
	align-items: center;
	margin-bottom: 16px;
}
.test-container {
	position: relative;
	display: flex;
	flex-direction: column;
	align-items: center;
    justify-content: center;
	width: 100%;
	height: 100%;
	padding: 0 15px 35px;
	border-radius: 6px;
	background-color: var(--background-sec);
	.close-btn {
		position: absolute;
		right: 15px;
		top: 15px;
		-webkit-appearance: none;
		appearance: none;
		background-color: transparent;
		transition: all 0.5s ease;
		display: inline-flex;
		justify-content: center;
		align-items: center;
		border-radius: 50%;
		font-size: 1rem;
		cursor: pointer;
		border: none;
		z-index: 1;
		color: var(--BrightRed);
		&:hover {
			filter: brightness(1.25);
		}
	}
	&::before {
		position: absolute;
		left: 25px;
		bottom: 10px;
		font-size: 0.75rem;
		content: "progress : " attr(data-progress) " %";
	}
	&::after {
		position: absolute;
		bottom: 10px;
		right: 25px;
		font-size: 0.75rem;
		color: inherit;
		content: attr(data-match);
	}
	h3 {
		flex-grow: 1;
		text-align: center;
		font-size: 1.5rem;
		line-height: 50vh;
		color: var(--LimeGreen);
	}

	.T-text {
		text-align: center;
		font-size: 1.5rem;
		line-height: 1.6;
		padding: 8px 0;
	}
	.T-form {
		flex-grow: 1;
		background-color: var(--background-pri);
		form {
			display: flex;
			height: 100%;
			gap: 8px;
			align-items: center;
		}
		input {
			height: 100%;
			flex-grow: 1;
			background-color: transparent;
			color: var(--text-color);
			font-size: 1.25rem;
			padding: 8px 16px;
			outline: none;
			transition: border 250ms ease;
			border-bottom: 2px solid var(--border-color);
			text-align: center;
			&:focus {
				border-bottom-color: var(--LimeGreen);
			}
		}
		button[type="submit"] {
			padding: 8px;
			background-color: transparent;
			outline: none;
			border: none;
			color: var(--text-color);
			width: 35px;
			height: 35px;
			cursor: pointer;
			&:hover {
				filter: brightness(1.25);
			}
			&:focus {
				outline: 1px solid var(--LimeGreen);
			}
		}
	}
	.T-wrapper {
		position: relative;
		width: 100%;
		height: 100%;
		padding-top: 35px;
		.T-submit {
				position: absolute;
				right: 0;
				bottom: 0;
				cursor: pointer;
			}
		form {
			display: flex;
			height: 80vh;
			gap: 8px;
			overflow-x: scroll;
			&::-webkit-scrollbar {
				 display: none;
			}
			.T-list {
				list-style: none;
				display: flex;
				flex-shrink: 0;
				flex-direction: column;
				justify-content: center;
				height: 100%;
				width: 50%;
				gap: 8px;
				margin: 0 auto;
				.T_row {
					display: flex;
					gap: 8px;
					align-items: center;
					justify-content: center;

				}
				.Item {
					flex-basis: 49%;
					height: 100%;
					display: flex;
					align-items: center;
					justify-content: center;
					border-radius: 6px;
					padding: 8px;
					background-color: var(--background-pri);
					font-size: 1.26rem;
					color: var(--text-color);
					transition: all 500ms ease;
					cursor: pointer;

				}
			}
			
		}
	}

	// reset btn
	button[type="reset"] {
		margin-top: 16px;
		width: 100%;
		padding: 8px;
		background-color: var(--BrightRed);
		outline: none;
		border: none;
		color: var(--text-color);
		border-radius: 6px;
		opacity: 0.8;
		cursor: pointer;
		&:hover {
			opacity: 1;
		}
		&:focus {
			opacity: 1;
			outline: 1px solid var(--BrightRed);
		}
	}
}
