.wrapper{
    max-width: 180px;
    min-height: 180px;
    padding: 0px 8px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: var(--background-sec);
    border: 2px solid transparent;
    border-radius: 6px;
    overflow: hidden;
    position: relative; 
}
.Hanzi-wrapper{ 
    max-width:180px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}
.quiz-info{
    position: absolute;
    top: 0;
    right: 0;  
    padding: 12px 6px;
    color: var(--LimeGreen);
    gap: 3px;
    z-index: 999;
}
.pinyin-wrapper{
    width: 100%;
    text-align: center;
    border-radius: 6px;
    padding: 8px;
    background-color: var(--background-pri);
    font-size: 1rem;
    overflow-x:auto;
    &::-webkit-scrollbar{display: none;}
}
.controler{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
     padding: 8px;
     background-color: var(--background-sec);
     transform: translateX(100%);
     transition: transform 0.350s linear;
     .btn{
         transition: color 0.350s linear;
         color: inherit;
         background-color: transparent;
         border: none;
         outline: none;
         font-size: 1rem;
         padding: 3px;
         cursor: pointer;
         &:hover{
            color: var(--LimeGreen);
         }
     }
     .right-section{
         margin-left: auto;
         display: flex;
         align-items: center;
         gap: 8px;
     }
}
.wrapper:hover .controler{transform: translateX(0);}