.wrapper {
	.form-header{
	display: flex;
	min-height: 40px;
	justify-content: space-evenly;
	align-items: center;
	font-size: 1.6rem;
	color: var(--LimeGreen);
		.close-form {
			-webkit-appearance: none;
			appearance: none;
			background-color: transparent;
			transition: all 0.5s ease;
			display: inline-flex;
			justify-content: center;
			align-items: center;
			border-radius: 50%;
			font-size: 1rem;
			cursor: pointer;
			border: none;
			color: var(--BrightRed);
			&:hover {
				filter: brightness(1.25);
			}
		}
	}
	form {
		flex-grow: 1;
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
		gap: 8px;
		.Field-group{
			display: flex;
			flex-direction: column;
			gap:8px;
		}
		.label {
			text-transform: capitalize;
			padding: 0 0 0 8px;
			position: relative;
			width: 88%;
			margin: 0 auto;
			top: 6px;
		}
		.form-btn-group {
			width: 50%;
			margin: 16px auto 0;
			gap: 5px;
			display: flex;
			justify-content: flex-end;
		}

		.btnSubmit {
			color: var(--LimeGreen);

			&:hover {
				border-color: var(--LimeGreen);
			}

			&[tabindex]:focus {
				outline-color: var(--LimeGreen);
			}
		}

		.btnReset {
			color: var(--BrightRed);

			&:hover {
				border-color: var(--BrightRed);
			}

			&[tabindex]:focus {
				outline-color: var(--BrightRed);
			}
		}

		button {
			flex-grow: 1;
			padding: 8px 16px;
			transition: color 0.5s ease;
			background-color: transparent;
			font-size: 1.25rem;
			border: 2px solid var(--border-color);
			border-radius: 6px;
			color: inherit;
			cursor: pointer;
			user-select: none;
			&:disabled {
				pointer-events: none;
				opacity: 0.35;
				cursor: not-allowed;
			}

			&:active {
				border-color: red;
			}
		}
	}
}

@media screen and (min-width: 920px) {
	.wrapper {
		form {
			.form-btn-group {
				margin-top: 5px;
			}

			button {
				height: unset;
				margin-left: 0;
				margin-top: 5px;
				padding: 8px;
			}
		}
	}
}




.input {
	display: block;
	width: 88%;
	margin: 0 auto;
	font-family: "Noto Serif SC", serif;
	-webkit-appearance: none;
	appearance: none;
	background-color: transparent;
	font-size: 1.6rem;
	font-weight: 300;
	border: 2px solid var(--border-color);
	padding: 16px 8px;
	color: inherit;
	border-radius: 6px;
	outline: 2px transparent solid;
	&[tabindex]:focus {
		outline-color: var(--LimeGreen);
	}
	&:not(:first-child) {
		margin-top: 16px;
	}
	&:disabled {
		cursor: not-allowed;
	}
}
